import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import './header.css';

export default function Header() {
    const [view, setView] = useState(false)

    let menuToggle = () => {
        setView(!view)
        if (!view) {
            document.getElementById("navbar").style.display = "none"
            document.getElementById("burger").classList.remove("change")
        } else {
            document.getElementById("navbar").style.display = "flex"
            document.getElementById("burger").classList.add("change")
        }
    }

    return (
        <header>
            <section onClick={menuToggle} id="burger" className="burger">
                <div className="bar1"></div>
                <div className="bar2"></div>
                <div className="bar3"></div>
            </section>
            <nav id="navbar">
                <Link to="/clients" className="link">Home</Link>
                <Link to="/clients/bmss" className="link">BMSS Business Advisors &amp; CPAs</Link>
                <Link to="/clients/abit" className="link">Abacus Technologies</Link>
                <Link to="/clients/pbs" className="link">Payroll &amp; Benefit Solutions</Link>
            </nav>
        </header>
    )
}
