import React, { useState } from 'react'

export default function Home(props) {
    // use filter?
    //let [search, setSearch] = useState("");
    let [results, setResults] = useState([]);

    let search = (e) => {
        if (e.target.value.length === 0) {
            setResults([])
        } else if (e.target.value.length >= 2) {
            let query = new RegExp(`\\b${e.target.value}`, "gi")
            
            let bmss = props.bmss.filter((client) => {
                return query.exec(client)
            })

            let abit = props.abit.filter((client) => {
                return query.exec(client)
            })

            let pbs = props.pbs.filter((client) => {
                return query.exec(client)
            })

            let fullClients = [...bmss, ...abit, ...pbs]

            let clients = fullClients.filter((client, index) => {
                if (fullClients.lastIndexOf(client) === index) {
                    return client
                }
            })

            let result = clients.map((client, index) => {
                return (
                    <div className={(index % 2) === 0 ? "even" : "odd"} key={index}>
                        <div className="main-detail">
                            <h2 className="client">{client}</h2>
                            <h3 className="col-1 row-1">BMSS Business Advisors &amp; CPAs</h3>
                            <h3 className="col-2 row-1">Abacus Technologies</h3>
                            <h3 className="col-3 row-1">Payroll &amp; Benefit Solutions</h3>
                            <p className="col-1 row-2">{props.bmss.includes(client) ? "Yes" : "No"}</p>
                            <p className="col-2 row-2">{props.abit.includes(client) ? "Yes" : "No"}</p>
                            <p className="col-3 row-2">{props.pbs.includes(client) ? "Yes" : "No"}</p>
                        </div >
                    </div>
                )
            })
            setResults(result)
        }
    }

    return (
        <div className="hero">
            <h1>Search for a client to see what companies they are using:</h1>
            <input className="search" type="text" onChange={(e) => {
                search(e)
            }} />
            {results}
        </div>
    )
}
